#boxWrapper-masterControls {
    width: 17.5%; /* same as it container */
    margin: 20px auto 10px;
}

#controlsMenu-masterControls {
    height: 85%;
    margin: 10px 10px;
    justify-content:center;
}

#pitchControlSection-masterControls {
    border-top: 1px solid;
    border-bottom: 1px solid;
    border-image: linear-gradient(to right, rgb(255 210 110 / 0.1) 0%, rgb(255 210 110 / 1) 50%, rgb(255 210 110 / 0.1) 100%);
    border-image-slice: 1;
}

.controlSection-masterControls .MuiGrid-item {
    margin: auto;
}

.controlSection-masterControls h4 {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    text-align:center;
    background-image: linear-gradient(#ffd26e, #feda88, #ffd26e);
    background-clip: text;
    color: transparent;
}