html {
    min-height: 100vh;
    background-repeat: no-repeat;
    background-image: linear-gradient(#353535, #191919);
}

.sectionWrapper {
    background-image: linear-gradient(to right, #171718, #2c2b2d);
}

.sectionHeading {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    text-align: center;
}

    .sectionHeading h1, .sectionHeading h2, .sectionHeading h3, .sectionHeading h4, .sectionHeading h5 {
        background-image: linear-gradient(#ffd26e, #feda88, #ffd26e);
        background-clip: text;
        color: transparent;
        margin: 5px auto;
    }

.css-sghohy-MuiButtonBase-root-MuiButton-root.button, .css-sghohy-MuiButtonBase-root-MuiButton-root.switch-button, .css-1hw9j7s.button, .css-1hw9j7s.switch-button{
    background-image: linear-gradient(to right, #222224, #4f4a50);
    color: #ffd26e;
}

.Mui-disabled.switch-button {
}

    .button:hover {
        background-image: linear-gradient(to right, #897430, #a9903b) !important;
    }

    .switch-button:hover {
        color: #ffffff !important;
    }

.smallButton {
    font-size: 0.7825rem !important;
    padding: 2px 5px !important;
}

.mediumButton {
    font-size: 0.8125rem !important;
    padding: 4px 10px !important;
}

.largeButton {
    font-size: 0.8425rem !important;
    padding: 6px 15px !important;
}

.textButton {
    margin: 0 !important;
    padding: 0 !important;
    color: #000000 !important;
}

    .textButton:hover {
        color: #ffd26e !important;
    }

    .textButton:disabled {
        color: #909090 !important;
    }

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* ------------------------------ top-UI ------------------------------ */
#topUI-app {
    max-width: 1720px; /* 1920px -200px (for edge margin)*/
    margin:auto;
}

#topUI-left-app, #topUI-right-app {
    width: 17.5%;
}

#topUI-center-app {
    width: 65%;
}
/* ------------------------------------------------------------------- */
