#title {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    text-transform: uppercase;
    text-align: center;
    background-image: linear-gradient(#ffd26e, #feda88, #ffd26e);
    background-clip: text;
    color: transparent;
    margin-bottom: -5px;
}

#subtitle {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-style: italic;
    text-align: center;
    background-image: linear-gradient(#ffd26e, #feda88, #ffd26e);
    background-clip: text;
    color: transparent;
    margin-top: -5px;
}
