#boxWrapper-selectionView {
    min-width: 700px;
    max-width: 1720px; /* 1920px -200px (for edge margin)*/
    margin: 10px auto;
}

#table-selection {
}

#tableHead-selection {
    background-color: #ffffff
}

    #tableHead-selection .tableCell-selection {
        border-style: none double;
        border-width: 4px;
        line-height: 0;
    }

    #tableHead-selection .tableCell-selection-cap {
        line-height: 0;
        padding: 0;
    }

.wordDisplayNavBtn-selection {
    height: 100%;
    width: 100%;
}



#tableBody-selection {
}

    #tableBody-selection .tableCell-selection {
        border-style: none solid;
        border-color: #ffd26e;
    }

.tableCell-selection-cap {
    border: none !important;
}

#editingSwitches-selection {
    float:right;

}

    #editingSwitches-selection .button {
        width: 120px;
    }

.settingsLabels-selection {
    float: left;
    text-align: left;
    background-image: linear-gradient(#ffd26e, #feda88, #ffd26e);
    background-clip: text;
    color: transparent;
}

.switchButton-editing-selection_active {
    background-image: linear-gradient(to right, #897430, #a9903b) !important;
    color: #ffffff !important;
}